.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  // gap: 1.5rem;
  padding: 3rem 15rem;
  height: 3rem;
  background-color: transparent;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 100;

  ul {
    display: flex;
    gap: 3rem;
    list-style: none;

    @media (max-width: 512px) {
      // padding: 3rem;
      display: none;
    }
  }

  @media (max-width: 768px) {
    padding: 3rem;
  }
}

.hamburger {
  display: none;

  @media only screen and (max-width: 512px) {
    display: block;
    // font-size: 2rem;
    cursor: pointer;
  }
}

.bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  background-color: white;
}
